// app/actions.ts
"use server";

import { PrismaClient } from '@prisma/client';
import { revalidatePath } from 'next/cache';
import { cookies } from 'next/headers';
// IMPORTANTE: Importamos UserPayload para corregir los errores rojos
import { signToken, verifyToken, UserPayload } from '@/lib/auth';
import { sendAuthEmail, sendRegistrationRequestEmail } from '@/lib/mail';

const globalForPrisma = global as unknown as { prisma: PrismaClient };
const prisma = globalForPrisma.prisma || new PrismaClient();
if (process.env.NODE_ENV !== 'production') globalForPrisma.prisma = prisma;

// --- UTILIDAD: OBTENER USUARIO ACTUAL (TIPADO CORRECTAMENTE) ---
async function getCurrentUser(): Promise<UserPayload | null> {
    const cookieStore = await cookies();
    const token = cookieStore.get('auth_token')?.value;
    if (!token) return null;
    return await verifyToken(token);
}

// --- AUTENTICACIÓN: PASO 1 (PEDIR CÓDIGO) ---
export async function requestLoginCode(email: string) {
    const user = await prisma.user.findUnique({ where: { email } });
    
    if (!user) {
        return { success: false, message: 'Usuario no encontrado. Debes registrarte primero.' };
    }

    // Generar código: 8 dígitos + 1 caracter especial
    const digits = Math.floor(10000000 + Math.random() * 90000000).toString();
    const specials = "%#-/*@";
    const specialChar = specials.charAt(Math.floor(Math.random() * specials.length));
    const secureCode = digits + specialChar;

    await prisma.user.update({
        where: { email },
        data: { token: secureCode } 
    });

    try {
        await sendAuthEmail(email, secureCode);
        return { success: true };
    } catch (error) {
        console.error("Error mailing:", error);
        return { success: false, message: 'Error enviando correo.' };
    }
}

// --- AUTENTICACIÓN: PASO 2 (LOGIN) ---
export async function loginAction(email: string, token: string) {
    const user = await prisma.user.findUnique({ where: { email } });

    if (!user || user.token !== token) {
        return { success: false, message: 'Código incorrecto o expirado' };
    }

    const jwt = await signToken({ 
        id: user.id, 
        email: user.email, 
        householdId: user.householdId 
    });

    const cookieStore = await cookies();
    cookieStore.set('auth_token', jwt, { 
        httpOnly: true, 
        secure: process.env.NODE_ENV === 'production',
        maxAge: 60 * 60 * 24 * 30, // 30 días
        path: '/' 
    });
    
    cookieStore.delete('is_demo_mode');
    return { success: true };
}

// --- REGISTRO ---
export async function requestRegistration(email: string, familyName: string) {
    const existing = await prisma.user.findUnique({ where: { email } });
    if (existing) {
        return { success: false, message: 'Correo ya registrado.' };
    }
    try {
        await sendRegistrationRequestEmail(email, familyName);
        return { success: true, message: 'Solicitud enviada al administrador.' };
    } catch (e) {
        return { success: false, message: 'Error de conexión.' };
    }
}

// --- SESIÓN ---
export async function setDemoModeAction() {
    const cookieStore = await cookies();
    cookieStore.set('is_demo_mode', 'true', { path: '/' });
    cookieStore.delete('auth_token');
    return { success: true };
}

export async function logoutAction() {
    const cookieStore = await cookies();
    cookieStore.delete('auth_token');
    cookieStore.delete('is_demo_mode');
    return { success: true };
}

// --- LEER DATOS ---
export async function getFinanceData() {
    const cookieStore = await cookies();
    const isDemo = cookieStore.get('is_demo_mode')?.value === 'true';
    if (isDemo) return { isDemo: true };

    const user = await getCurrentUser();
    if (!user) throw new Error("No autorizado");

    const householdId = user.householdId; // Ya no debe marcarse en rojo

    const accounts = await prisma.account.findMany({ where: { householdId } });
    const categories = await prisma.category.findMany({ where: { householdId } });
    const transactions = await prisma.transaction.findMany({ 
        where: { householdId }, orderBy: { date: 'desc' } 
    });
    const fixedRules = await prisma.fixedRule.findMany({ where: { householdId } });
    const simulatorItems = await prisma.simulatorItem.findMany({ where: { householdId } });

    return {
        isDemo: false,
        accounts,
        categories: categories.map(c => ({...c, subCategories: c.subCategories ? JSON.parse(c.subCategories) : []})),
        transactions: transactions.map(t => ({...t, date: t.date.toISOString()})),
        fixedRules,
        simulatorItems
    };
}

// --- TRANSACCIONES ---
export async function createTransaction(data: any) {
    const user = await getCurrentUser();
    if (!user) return; // Si no hay usuario, cancelamos
    const householdId = user.householdId; // TypeScript ahora sabe que user existe aquí

    const date = new Date(data.date);

    if (data.type === 'transfer') {
        // Transferencia
        await prisma.transaction.create({
            data: { householdId, type: 'expense', amount: data.amount, accountId: data.accountId, description: `Transferencia a: ${data.destAccountName}`, date, isTransfer: true, relatedAccountId: data.relatedAccountId, didWithdraw: true }
        });
        await prisma.account.update({ where: { id: data.accountId }, data: { balance: { decrement: data.amount } } });

        await prisma.transaction.create({
            data: { householdId, type: 'income', amount: data.amount, accountId: data.relatedAccountId, description: `Recibido de: ${data.originAccountName}`, date, isTransfer: true, relatedAccountId: data.accountId, didWithdraw: false }
        });
        await prisma.account.update({ where: { id: data.relatedAccountId }, data: { balance: { increment: data.amount } } });
    } else {
        // Normal
        const shouldUpdateBalance = data.type === 'expense' ? data.didWithdraw : true;
        await prisma.transaction.create({
            data: {
                householdId, type: data.type, amount: data.amount, budgetedAmount: data.budgetedAmount, accountId: data.accountId, categoryId: data.categoryId, description: data.description, date, isFixed: data.isFixed||false, isSavings: data.isSavings||false, didWithdraw: data.didWithdraw??true
            }
        });

        if (shouldUpdateBalance) {
            const multiplier = data.type === 'income' ? 1 : -1;
            await prisma.account.update({ where: { id: data.accountId }, data: { balance: { increment: data.amount * multiplier } } });
        }

        if (data.isSavings) {
            let savingsAcc = await prisma.account.findFirst({ where: { type: 'savings_virtual', householdId } });
            if (!savingsAcc) {
                savingsAcc = await prisma.account.create({ data: { householdId, name: 'Bolsillo Ahorros Global', bank: 'Sistema', type: 'savings_virtual', owner: 'Familia', color: '#3b82f6' } });
            }
            await prisma.account.update({ where: { id: savingsAcc.id }, data: { balance: { increment: data.amount } } });
        }
    }
    revalidatePath('/');
}

// --- CRUD GENERKOS (Tipados) ---

export async function createAccount(data: any) {
    const user = await getCurrentUser();
    if (user) {
        await prisma.account.create({ data: { ...data, householdId: user.householdId } });
        revalidatePath('/');
    }
}

export async function updateAccount(data: any) {
    await prisma.account.update({
        where: { id: data.id },
        data: { name: data.name, bank: data.bank, type: data.type, color: data.color }
    });
    revalidatePath('/');
}

export async function deleteAccount(id: string) {
    await prisma.account.delete({ where: { id } });
    revalidatePath('/');
}

export async function createCategory(data: any) {
    const user = await getCurrentUser();
    if (user) {
        await prisma.category.create({ data: { 
            name: data.name, type: data.type, subCategories: JSON.stringify(data.subCategories || []),
            householdId: user.householdId 
        }});
        revalidatePath('/');
    }
}

export async function updateCategory(data: any) {
    await prisma.category.update({
        where: { id: data.id },
        data: { name: data.name, type: data.type, subCategories: JSON.stringify(data.subCategories) }
    });
    revalidatePath('/');
}

export async function deleteCategory(id: string) {
    await prisma.category.delete({ where: { id } });
    revalidatePath('/');
}

export async function deleteTransaction(id: string) {
    const tx = await prisma.transaction.findUnique({ where: { id } });
    if (!tx) return;

    await prisma.transaction.delete({ where: { id } });

    if (tx.accountId && tx.didWithdraw && !tx.isTransfer) {
        const multiplier = tx.type === 'income' ? -1 : 1; 
        await prisma.account.update({
            where: { id: tx.accountId },
            data: { balance: { increment: tx.amount * multiplier } }
        });
    }
    revalidatePath('/');
}

export async function createFixedRule(data: any) {
    const user = await getCurrentUser();
    if (user) {
        await prisma.fixedRule.create({ data: { ...data, householdId: user.householdId } });
        revalidatePath('/');
    }
}

export async function updateFixedRule(data: any) {
    await prisma.fixedRule.update({
        where: { id: data.id },
        data: { description: data.description, budgetedAmount: data.budgetedAmount, dayOfMonth: data.dayOfMonth, isActive: data.isActive }
    });
    revalidatePath('/');
}

export async function deleteFixedRule(id: string) {
    await prisma.fixedRule.delete({ where: { id } });
    revalidatePath('/');
}

export async function saveSimulatorItem(data: any) {
    const user = await getCurrentUser();
    if (user) {
        const existing = await prisma.simulatorItem.findUnique({ where: { id: data.id } });
        if (existing) {
            await prisma.simulatorItem.update({ where: { id: data.id }, data: { name: data.name, amount: data.amount } });
        } else {
            await prisma.simulatorItem.create({ data: { ...data, householdId: user.householdId } });
        }
        revalidatePath('/simulator');
    }
}

export async function deleteSimulatorItem(id: string) {
    await prisma.simulatorItem.delete({ where: { id } });
    revalidatePath('/simulator');
}

