"use client";
import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { loginAction, setDemoModeAction, requestLoginCode, requestRegistration, seedDatabase } from '@/app/actions';
import { useRouter } from 'next/navigation';
import { Lock, Eye, Mail, ArrowRight, UserPlus, Users } from 'lucide-react';
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";

export default function LoginPage() {
  const router = useRouter();
  const [email, setEmail] = useState('');
  const [token, setToken] = useState('');
  const [familyName, setFamilyName] = useState('');
  
  // Estados de flujo
  const [step, setStep] = useState(1); // 1: Email, 2: Código
  const [loading, setLoading] = useState(false);

  // --- LOGIN ---
  const handleRequestCode = async (e: React.FormEvent) => {
      e.preventDefault();
      setLoading(true);
      const res = await requestLoginCode(email);
      setLoading(false);
      
      if (res.success) {
          setStep(2);
      } else {
          alert(res.message);
      }
  };

  const handleLogin = async (e: React.FormEvent) => {
      e.preventDefault();
      setLoading(true);
      const res = await loginAction(email, token);
      if (res.success) {
          router.push('/'); 
      } else {
          alert("Código incorrecto");
          setLoading(false);
      }
  };

  // --- REGISTRO ---
  const handleRegister = async (e: React.FormEvent) => {
      e.preventDefault();
      setLoading(true);
      const res = await requestRegistration(email, familyName);
      setLoading(false);
      
      if (res.success) {
          alert("✅ Solicitud enviada. El administrador (Jhonattan) debe autorizarte.");
          setEmail(''); setFamilyName('');
      } else {
          alert("Error: " + res.message);
      }
  };

  const handleDemo = async () => {
      setLoading(true);
      await setDemoModeAction();
      router.push('/'); 
  };

  return (
    <div className="min-h-screen bg-black flex items-center justify-center p-4">
        <div className="w-full max-w-md bg-zinc-900 border border-zinc-800 p-8 rounded-2xl shadow-2xl">
            <div className="text-center mb-6">
                <h1 className="text-3xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-indigo-400 to-emerald-400">
                    Finance Home
                </h1>
            </div>

            <Tabs defaultValue="login" className="w-full">
                <TabsList className="grid w-full grid-cols-2 bg-zinc-950 mb-6">
                    <TabsTrigger value="login">Iniciar Sesión</TabsTrigger>
                    <TabsTrigger value="register">Solicitar Acceso</TabsTrigger>
                </TabsList>

                {/* LOGIN TAB */}
                <TabsContent value="login">
                    {step === 1 ? (
                        <form onSubmit={handleRequestCode} className="space-y-4">
                            <Input 
                                type="email" placeholder="Correo registrado" 
                                className="bg-black border-zinc-800 h-12 text-white"
                                value={email} onChange={e => setEmail(e.target.value)} required
                            />
                            <Button type="submit" disabled={loading} className="w-full h-12 bg-indigo-600 hover:bg-indigo-700 font-bold">
                                {loading ? 'Enviando...' : 'Enviar Código'} <ArrowRight className="ml-2 h-4 w-4" />
                            </Button>
                        </form>
                    ) : (
                        <form onSubmit={handleLogin} className="space-y-4">
                            <div className="text-center text-zinc-400 text-sm mb-2">Código enviado a {email}</div>
                            <Input 
                                type="text" placeholder="TOKEN" 
                                className="bg-black border-zinc-800 h-14 text-center text-2xl tracking-widest text-emerald-400 font-mono"
                                value={token} onChange={e => setToken(e.target.value)} required
                            />
                            <Button type="submit" disabled={loading} className="w-full h-12 bg-emerald-600 hover:bg-emerald-700 font-bold">
                                {loading ? 'Validando...' : 'Entrar'}
                            </Button>
                            <button type="button" onClick={()=>setStep(1)} className="text-xs text-indigo-400 w-full mt-2 hover:underline">Cambiar correo</button>
                        </form>
                    )}
                </TabsContent>

                {/* REGISTER TAB */}
                <TabsContent value="register">
                    <form onSubmit={handleRegister} className="space-y-4">
                        <div className="space-y-2">
                             <label className="text-xs text-zinc-400 ml-1">Tu Correo</label>
                             <div className="relative">
                                <Mail className="absolute left-3 top-3 h-5 w-5 text-zinc-600" />
                                <Input className="pl-10 bg-black border-zinc-800" type="email" required value={email} onChange={e=>setEmail(e.target.value)} />
                             </div>
                        </div>
                        <div className="space-y-2">
                             <label className="text-xs text-zinc-400 ml-1">Nombre Familia / Grupo</label>
                             <div className="relative">
                                <Users className="absolute left-3 top-3 h-5 w-5 text-zinc-600" />
                                <Input className="pl-10 bg-black border-zinc-800" placeholder="Ej: Casita Centro" required value={familyName} onChange={e=>setFamilyName(e.target.value)} />
                             </div>
                        </div>
                        <Button type="submit" disabled={loading} className="w-full h-12 bg-zinc-800 hover:bg-zinc-700 border border-zinc-700">
                             <UserPlus className="mr-2 h-4 w-4" /> Solicitar Registro
                        </Button>
                    </form>
                </TabsContent>
            </Tabs>

            <div className="mt-8 pt-6 border-t border-zinc-800">
                <Button variant="outline" onClick={handleDemo} className="w-full border-zinc-700 hover:bg-zinc-800 text-zinc-300">
                    <Eye className="mr-2 h-4 w-4" /> Ver Demo 2026
                </Button>
               
                <Button onClick={() => seedDatabase()} className="mt-2 text-xs bg-red-900">Emergency Seed</Button>
            </div>
        </div>
    </div>
  );
}